import YouTube from 'react-youtube';

export default class Table extends React.Component {
    constructor(props) {
       super(props)
       this.state = {
          navigations: this.props.navigations
       }
        // this._onReady = this._onReady.bind(this);
        // this._onStateChange = this._onStateChange(this);
    }

    // _onReady(event, frame) {
    //     this.setState({
    //     duration: event.target.getDuration(), 
    //     currentTime: event.target.getCurrentTime(),
    //     videoTarget: event.target,
    //     })
    // }

    // _onStateChange(event) {
    //     const { duration } = this.state;
    //     this.setState({
    //       videoState: event.target.getPlayerState(),
    //     })
    //     if (duration != event.target.getDuration())
    //       this.setState({
    //         duration: event.target.getDuration(),
    //         videoTarget: event.target,
    //         currentTime: 0,
    //       })
    //   }
    
    renderTableHeader() {
       let header =['id', 'what you said', 'matching subtitle', 'frame']
       return header.map((key, index) => {
          return <th key={index}>{key.toUpperCase()}</th>
       })
    }
 
    renderTableData() {
        if (this.props.navigations != null){
            return this.props.navigations.map((navigation) => {
                const { id, transcript, subtitle, frame } = navigation //destructuring
                // console.log(frame);
                return (
                    <tr key={id}>
                        <td>{id}</td>
                        <td>{transcript}</td>
                        <td>{subtitle}</td>
                        <td>
                            {/* <YouTube
                                className="preview"
                                videoId={this.props.videoID}
                                opts={{playerVars: {autoplay: 1, start: '10'}}}
                            /> */}
                            {frame}
                        </td>
                    </tr>
                )
            })
        }
    }
 
    render() {
       return (
          <div>
             <table id='navigations'>
                <tbody>
                   <tr>{this.renderTableHeader()}</tr>
                   {this.renderTableData()}
                </tbody>
             </table>
          </div>
       )
    }
 }
 
