import React from "react";
import Popup from "reactjs-popup";
import TimeField from 'react-simple-timefield';
import './App.css';
import Dropdown from 'react-dropdown'
import 'react-dropdown/style.css'
import YouTube from 'react-youtube';
import axios from 'axios';

const options = [
    {value: 'error0', label: 'did not recognize my voice clearly'},
    { value: 'error1', label: 'showed few seconds earlier than that' },
    { value: 'error2', label: 'showed few seconds later than that' },
    { value: 'error3', label: 'showed another scene with the same object'},
    { value: 'error4', label: 'showed another scene with the same action'},
    { value: 'error5', label: 'Others'}
];
  
export default class Modal extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            selectedOption: null,
            input: null,
            time: '00:00',
          };
        
        this.handleChange = this.handleChange.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);
        this.onTimeChange = this.onTimeChange.bind(this);
        this._onSelect = this._onSelect.bind(this);
      }

      
      handleChange(event) {
        this.setState({input: event.target.value});
      }
    
      handleSubmit(event, close) {
        event.preventDefault();
        const {selectedOption, input,} = this.state;
        const {navigation, open, closeModal, navigationID, videoID, startListening} = this.props;
        const { navigationCount, transcript, subtitle, frame } = navigation //destructuring
        if(!selectedOption || ((selectedOption === 'error5' || selectedOption === 'error0') && !input)){  
            event.preventDefault();
            alert('Please fill all the information');
        }
        else{
            //TODO axios post to the backend
            console.log('subtitle_time is ',frame );
            axios.put('https://rubyslippers.kixlab.org/backend/navigations/'+ navigationID +'/add_evaluation/', {
                error_type: selectedOption,
                error_details: input,
                subtitle: subtitle,
                subtitle_time: frame,
                correct: false,
            });
            this.setState({
                selectedOption: null,
                input: null,
                time: '00:00',
            });
            startListening();
            close();
        }
        return false;
      }

      onTimeChange(event, time) {
        console.log("time is", this.state.time);
        this.setState({time});
      }
    
      _onSelect = selectedOption => {
        this.setState(
          { selectedOption: selectedOption.value },
          () => console.log(`Option selected:`, this.state.selectedOption)
        );
      };


    render() {
        const {selectedOption, time} = this.state;
        const {navigation, open, closeModal, navigationID, videoID, startListening} = this.props;
        if(open){
            const { id, transcript, subtitle, frame } = navigation; //destructuring
            return (
                <Popup open = {open}  closeOnDocumentClick= {false}>
                    {close => (
                    <div className="modal">
                        {/* <a className="close" onClick={this.props.closeModal}>
                        &times;
                        </a> */}
                        <div className="header"> Navigation Evaluation {id}/5 </div>
                        <div className="content">
                        {" "}
                        Is this where you wanted to go?
                        </div>
                        <div className="actions">
                            <button className="button" onClick={() => { console.log("modal closed "); closeModal();
                                axios.put('https://rubyslippers.kixlab.org/backend/navigations/'+ navigationID +'/add_evaluation/', {
                                    correct: true,
                                    subtitle: subtitle,
                                    subtitle_time: frame,});
                                    startListening();}}>
                                Yes
                            </button>
                            <Popup
                                trigger={<button className="button"> No </button>}
                                position="top center"
                                closeOnDocumentClick= {false}
                                modal
                            >
                                <div className="nested-modal">
                                    <div className="content">
                                    {" "}
                                    Please tell us your intention behind "{transcript}" and the type of error system made
                                    </div>
                                    <form onSubmit={this.handleSubmit}>
                                        <div className="form">
                                            <label>
                                            Find the time of frame you originally wanted to go to
                                            </label>
                                            <div className="destination">
                                                <TimeField value={time} style={{width: '60px', textAlign: 'center'}} onChange={this.onTimeChange} />
                                                <YouTube
                                                    className="modal-video"
                                                    videoId={videoID}
                                                    opts={{playerVars: {autoplay: 0, start: '10'}}}
                                                />
                                            </div>
                                            <br />
                                            <label>
                                            I wanted to see the above frame but the system
                                            </label>
                                            <Dropdown options={options} onChange={this._onSelect} value={selectedOption} placeholder="Select an option" />
                                            {this.state.selectedOption === "error5" ? <input type="text" value={this.state.input} onChange={this.handleChange} placeholder="Please describe the errors"/> : null}
                                            {this.state.selectedOption === "error0" ? <input type="text" value={this.state.input} onChange={this.handleChange} placeholder="I originally said ..."/> : null}
                                            
                                        </div><br />
                                        <button className="button" onClick={(event) => {this.handleSubmit(event, closeModal); return false;}}>
                                            Submit
                                        </button>
                                    </form>
                                </div>
                            </Popup>
                        </div>
                    </div>
                    )}
                </Popup>
            )
        }
        else{
            return (null);
        }
        }};